import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import Select from 'react-select';
import { PRINICIPAL_OFFICE_ADDRESS } from '../../../../../constants'; 
import CustomTextField from '../../../Layout/CustomTextField';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const PrincipalPlaceOfBusiness = ({ getValueAddressType, onChangeAddress, ausStateOptions, countryOptions }) => {
    
    const arr = ['unit_no', 'street_name', 'street_no', 'suburb', 'state', 'postcode', 'country'];
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const errMsgsPrinOffice = {
        type : invFormStatus.errors?.type? invFormStatus.errors?.type[PRINICIPAL_OFFICE_ADDRESS]: [],
        unit_no : invFormStatus.errors?.unit_no? invFormStatus.errors?.unit_no[PRINICIPAL_OFFICE_ADDRESS]: [],
        street_no : invFormStatus.errors?.street_no? invFormStatus.errors?.street_no[PRINICIPAL_OFFICE_ADDRESS]: [],
        street_name : invFormStatus.errors?.street_name? invFormStatus.errors?.street_name[PRINICIPAL_OFFICE_ADDRESS]: [],
        suburb : invFormStatus.errors?.suburb? invFormStatus.errors?.suburb[PRINICIPAL_OFFICE_ADDRESS]: [],
        state : invFormStatus.errors?.state? invFormStatus.errors?.state[PRINICIPAL_OFFICE_ADDRESS]: [],
        postcode : invFormStatus.errors?.postcode? invFormStatus.errors?.postcode[PRINICIPAL_OFFICE_ADDRESS]: [],
        country : invFormStatus.errors?.country? invFormStatus.errors?.country[PRINICIPAL_OFFICE_ADDRESS]: [],
    }

    useEffect(() => {
        return () => {
            for(let i = 0; i < arr.length; i++){
                onChangeAddress({ name: arr[i], value: '', addressType: PRINICIPAL_OFFICE_ADDRESS })
            }
        }
    }, []);

    const countryValue = () => {
        const findCountry = getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "country");
        
        const value = countryOptions.find((country) => {
            if(findCountry){
              return  country.label?.toLowerCase() === findCountry.toLowerCase();
            }
            return false
        });
        return value || null;
    }

    const ausStateValue = () => {
        const findState = getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "state");
        
        const value = ausStateOptions().find((eachState) => {
            if(findState){
              return  eachState.value?.toLowerCase() === findState.toLowerCase();
            }
            return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onChangeAddress({ name:'country', value: value.label, addressType: PRINICIPAL_OFFICE_ADDRESS })
    }

    const onChangeAusStates = (value) => {
        onChangeAddress({ name:'state', value: value.value, addressType: PRINICIPAL_OFFICE_ADDRESS })
    }
    
    return (
    <div>
        <h6 className="u-padding-10y">Full Postal Address</h6>
            <div className="sub__form">
                <CustomTextField
                    placeholder="Unit No./Level"
                    id="sec-d-business-unit-no"
                    label="Unit No./Level"
                    asterisk=""
                    name="unit_no"
                    value={getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "unit_no")}
                    onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: PRINICIPAL_OFFICE_ADDRESS })}
                    formErrors={errMsgsPrinOffice}
                />

                <CustomTextField
                    placeholder="Street Number"
                    id="sec-d-business-street-number"
                    label="Street Number"
                    name="street_no"
                    asterisk=""
                    value={getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "street_no")}
                    onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: PRINICIPAL_OFFICE_ADDRESS })}
                    formErrors={errMsgsPrinOffice}
                />

                <CustomTextField
                    placeholder="Street Name"
                    id="sec-d-business-street-name"
                    label="Street Name"
                    name="street_name"
                    asterisk=""
                    value={getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "street_name")}
                    onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: PRINICIPAL_OFFICE_ADDRESS })}
                    formErrors={errMsgsPrinOffice}
                />
                
                <CustomTextField
                    placeholder="Suburb/Town"
                    id="sec-d-business-suburb"
                    label="Suburb/Town"
                    type="form-input"
                    asterisk=""
                    name="suburb"
                    value={getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "suburb")}
                    onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: PRINICIPAL_OFFICE_ADDRESS })}
                    formErrors={errMsgsPrinOffice}
                /> 

                <div className="form-input country__dropdown">
                    <label>State</label>
                    <Select
                        className={errMsgsPrinOffice?.state?.length && 'incorrect'}
                        id="select-dropdown-country"
                        name="state"
                        autoFocus={false}
                        placeholder="Select a State"
                        options={ausStateOptions()}
                        value={ausStateValue()}
                        onChange={onChangeAusStates}                    />
                    {errMsgsPrinOffice?.state?.length?
                    <div className="input__desc">
                        {renderErrorTxt(errMsgsPrinOffice?.state)}
                    </div>: null}
                </div>

                <CustomTextField
                    placeholder="Postcode"
                    id="sec-d-business-postcode"
                    label="Postcode"
                    type="form-input"
                    asterisk=""
                    name="postcode"
                    value={getValueAddressType(PRINICIPAL_OFFICE_ADDRESS, "postcode")}
                    onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: PRINICIPAL_OFFICE_ADDRESS })}
                    formErrors={errMsgsPrinOffice}
                /> 

                <div className="country__dropdown u-margin-bottom">
                    <label>Country</label>
                    <Select
                        className={errMsgsPrinOffice?.country?.length && 'incorrect'}
                        id="select-dropdown-country"
                        name="country"
                        autoFocus={false}
                        options={countryOptions}
                        placeholder="Select a Country"
                        value={countryValue()}
                        onChange={onChangeSelectHandler}
                    />

                    {errMsgsPrinOffice?.country?.length?
                    <div className="input__desc">
                        {renderErrorTxt(errMsgsPrinOffice?.country)}
                    </div>: null}
                </div>
            </div>
    </div>
  )
}

export default PrincipalPlaceOfBusiness;