import React from 'react';
import { useSelector } from 'react-redux';
import { AUSTRALIAN_LISTED_COMPANY_D, MAJOR_OWNED_SUBSIDIARY_D, NOT_A_REGULATED_COMPANY_D, REGULATED_COMPANY_D } from '../../../../../constants';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const RegulatoryDetails = ({ onFormChange }) => {

    //secD reducer store
    const secD = useSelector((store) => store.secD);
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const onChangeFn = ({ name, value }) =>{
        const convrtToNum = parseInt(value);
        onFormChange({ name, value: convrtToNum });
    }

  return (
    <div className="section__container">
      <h4 className="section__container--subtitle">D.2 Regulatory / Listing Details</h4>
      
      <div className="section__container--box">
          <div>
                <h6>Is the company regulated or listed?<span>*</span></h6>

                <CustomRadioInput
                    name="company_regulation_detail"
                    id="company-not-regulated"
                    content="Company is not regulated or listed"
                    val={NOT_A_REGULATED_COMPANY_D} 
                    checked={secD?.company_regulation_detail === NOT_A_REGULATED_COMPANY_D}
                    onChangeFn={onChangeFn}
                />

                <CustomRadioInput
                    type="row-input"
                    name="company_regulation_detail"
                    id="company-regulated"
                    content="Regulated company - A company whose activities are subject to the oversight of a Commonwealth, State or Territory statutory regulator. In this context “Regulated” means subject to supervision beyond that provided by ASIC as a company registration body. For example, Australian Financial Services Licensee (AFSL), Australian Credit Licensee (ACL) or Registrable Superannuation Entity (RSE) Licensee"
                    val={REGULATED_COMPANY_D}
                    checked={secD?.company_regulation_detail === REGULATED_COMPANY_D}
                    onChangeFn={onChangeFn}
                />

                <CustomRadioInput
                    name="company_regulation_detail"
                    id="australian-listed"
                    content="Australian listed company"
                    val={AUSTRALIAN_LISTED_COMPANY_D}
                    checked={secD?.company_regulation_detail === AUSTRALIAN_LISTED_COMPANY_D}
                    onChangeFn={onChangeFn}
                />

                <CustomRadioInput
                    name="company_regulation_detail"
                    id="majority-owned-subsidiary"
                    content="Majority-owned subsidiary of an Australian listed company"
                    val={MAJOR_OWNED_SUBSIDIARY_D}
                    checked={secD?.company_regulation_detail === MAJOR_OWNED_SUBSIDIARY_D}
                    onChangeFn={onChangeFn}
                />
                {invFormStatus.errors?.company_regulation_detail? <div className="input__desc">
                    {renderErrorTxt(invFormStatus.errors?.company_regulation_detail)}
                </div>: null} 
          </div>
      </div>
  </div>
  )
}

export default RegulatoryDetails;