import React from 'react';
import { useSelector } from 'react-redux';
import { directorDetails } from '../../../../../reducers/Investor-Application-Form/secDReducer';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import Directors from '../Directors';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import MoreThanFourDirectors from '../MoreThanFourDirectors';
import { NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E } from '../../../../../constants';

const DirectorsSection = ({ onFormChange, investorFormId }) => {

  const secD = useSelector((store) => store.secD);
  const invFormStatus = useSelector((store) => store.invFormStatus)

  const addDirectorDetails = (numOfDirectors) => {

    let upd = [];
    const prevDirLen = secD?.directors_details?.length || 0;
    
    if(prevDirLen > numOfDirectors){
      upd = secD?.directors_details?.filter((each, index) => (index + 1) <= numOfDirectors);

      onFormChange({
        name:"directors_details",
        value:[...upd]
      });
      return
    }
    else if(prevDirLen === numOfDirectors){
      return
    }
    else{
      const newLen = numOfDirectors - prevDirLen;
      upd = [...secD?.directors_details];

      const newArr = Array.from({ length: newLen }, () => ({
        ...directorDetails
      }))

      onFormChange({
        name:"directors_details",
        value:[...upd, ...newArr]
      })
      return
    }
  }

  const onChangeDirector = ({ name, value, directorId }) => {
    
    const updIndex = directorId - 1;
    const upd = [...secD?.directors_details];
    upd[updIndex][name] = value;

    onFormChange({
      name: "directors_details",
      value: [...upd]
    });
    return
  }

  const renderDirectors = () => { 
    return secD?.directors_details?.map((item, index)=> {
        const tempIndex = index + 1;
        const uniqId = tempIndex //item?.id || tempIndex
        if((index < secD?.number_of_directors) && (secD?.number_of_directors !== NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E)){ //phase1 fix. remove this condition in phase-2
          return <Directors 
                      key={uniqId}
                      directorId={uniqId}
                      directorDetails={item}
                      onChangeDirector={onChangeDirector}
                      itemIndex={index}
                    /> 
        }
        return null
    })
  }

  const onChangeRadioFn = ({ name, value }) => {
    const convrtToNum = parseInt(value);
    onFormChange({
      name,
      value: convrtToNum
    })

    if(convrtToNum > 0){
      //update number if directors
      addDirectorDetails(convrtToNum);
    }
    else if(convrtToNum === NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E){
      addDirectorDetails(0);
    }

  }
    
  return (
    <div className="section__container">
      <h4 className="section__container--subtitle">D.4 Directors (Proprietary Companies Only)</h4>

      <div className="section__container--box">
          <h6>How many directors are there?<span>*</span></h6>
          <div className="u-margin-bottom row-space-between">
            <CustomRadioInput
                name="number_of_directors"
                id="sec-d-directors-1"
                content="1"
                val="1"
                checked={secD?.number_of_directors === 1}
                onChangeFn={onChangeRadioFn}
            />
            <CustomRadioInput
                name="number_of_directors"
                id="sec-d-directors-2"
                content="2"
                val="2"
                checked={secD?.number_of_directors === 2}
                onChangeFn={onChangeRadioFn}
            />
            <CustomRadioInput
                name="number_of_directors"
                id="sec-d-directors-3"
                content="3"
                val="3"
                checked={secD?.number_of_directors === 3}
                onChangeFn={onChangeRadioFn}
            />
            <CustomRadioInput
                name="number_of_directors"
                id="sec-d-directors-4"
                content="4"
                val="4"
                checked={secD?.number_of_directors === 4}
                onChangeFn={onChangeRadioFn}
            />
            <CustomRadioInput
                name="number_of_directors"
                id="sec-d-directors-more"
                content="more than 4"
                val={`${NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E}`}
                checked={secD?.number_of_directors === NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E}
                onChangeFn={onChangeRadioFn}
            />
          </div>
          <React.Fragment>
          {invFormStatus.errors?.number_of_directors? <div className="input__desc">
              {renderErrorTxt(invFormStatus.errors?.number_of_directors)}
            </div>: null}

            {secD?.number_of_directors === NO_OF_DIRECTORS_MORE_THAN_4_SEC_D_E?
            <>
              <h6 className="u-padding-y">If more than four directors, please provide full names of all the directors on a separate sheet and upload</h6>
              <MoreThanFourDirectors investorFormId={investorFormId} />
            </>
            : null}
          </React.Fragment>
          
          {renderDirectors()}

          {/* {Array.from({ length: numOfDirectors }, (_, index) => <Directors directorId ={index} />)} */}
      </div>
  </div>
  )
}

export default DirectorsSection;