import React from 'react';
import { useSelector } from 'react-redux';
import Select from 'react-select';
import {
    PRINCIPAL_PLACE_SAME_REGISTERED_ADDRESS,
    PRINCIPAL_PLACE_NOT_SAME_REGISTERED_ADDRESS,
    REGISTERED_OFFICE_ADDRESS,
    AUSTRALIAN_STATES,
} from '../../../../../constants';
import PrincipalPlaceOfBusiness from './PrincipalPlaceOfBusiness';
import CustomCheckbox from '../../../Layout/CustomCheckbox';
import CustomTextField from '../../../Layout/CustomTextField';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import { replaceAllWhiteSpaces } from '../../../../../utils/regexFuncs';

const AustralianCompanyDetails = ({ onFormChange }) => {

    //secD reducer store
    const secD = useSelector((store) => store.secD);
    const invFormStatus = useSelector((store) => store.invFormStatus);

    const countryOptions = [{id:1, value:'Australia', label:'Australia'}]

    const ausStateOptions = () => {
        const formattedOptions = AUSTRALIAN_STATES.map((each, index)=>{
            return { id: index+1, label: each, value: each}
        })
        return formattedOptions
    }

    const errMsgsregOffice = {
        type : invFormStatus.errors?.type? invFormStatus.errors?.type[REGISTERED_OFFICE_ADDRESS]: [],
        unit_no : invFormStatus.errors?.unit_no? invFormStatus.errors?.unit_no[REGISTERED_OFFICE_ADDRESS]: [],
        street_no : invFormStatus.errors?.street_no? invFormStatus.errors?.street_no[REGISTERED_OFFICE_ADDRESS]: [],
        street_name : invFormStatus.errors?.street_name? invFormStatus.errors?.street_name[REGISTERED_OFFICE_ADDRESS]: [],
        suburb : invFormStatus.errors?.suburb? invFormStatus.errors?.suburb[REGISTERED_OFFICE_ADDRESS]: [],
        state : invFormStatus.errors?.state? invFormStatus.errors?.state[REGISTERED_OFFICE_ADDRESS]: [],
        postcode : invFormStatus.errors?.postcode? invFormStatus.errors?.postcode[REGISTERED_OFFICE_ADDRESS]: [],
        country : invFormStatus.errors?.country? invFormStatus.errors?.country[REGISTERED_OFFICE_ADDRESS]: [],
      }


    const countryValue = () => {
        const findCountry = getValueAddressType(REGISTERED_OFFICE_ADDRESS, "country");
        
        const value = countryOptions.find((country) => {
            if(findCountry){
              return  country.label?.toLowerCase() === findCountry.toLowerCase();
            }
            return false
        });
        return value || null;
    }

    const ausStateValue = () => {
        const findState = getValueAddressType(REGISTERED_OFFICE_ADDRESS, "state");
        
        const value = ausStateOptions().find((eachState) => {
            if(findState){
              return  eachState.value?.toLowerCase() === findState.toLowerCase();
            }
            return false
        });
        return value || null;
    }

    const onChangeSelectHandler = (value) => {
        onChangeAddress({ name:'country', value: value.label, addressType: REGISTERED_OFFICE_ADDRESS })
    }

    const onChangeAusStates = (value) => {
        onChangeAddress({ name:'state', value: value.value, addressType: REGISTERED_OFFICE_ADDRESS })
    }

    //For text input fields
    const onChangeHandler = ({ name, value }) => {
        onFormChange({ name, value });
    }

    const onChangeACN = ({name, value}) => {
        const trimmedVal = replaceAllWhiteSpaces(value)
        onChangeHandler({name, value:trimmedVal})
    }

    //FOR SOLO CHECKBOX
    const handleChangeCheckbox = ({ name }) => { 
        // const convrtToNum = value && parseInt(value);
        if(secD?.is_address_of_registered_place_same_as_primary !== PRINCIPAL_PLACE_SAME_REGISTERED_ADDRESS){
            onFormChange({ name, value: PRINCIPAL_PLACE_SAME_REGISTERED_ADDRESS });
            return;
        }
        onFormChange({ name, value: PRINCIPAL_PLACE_NOT_SAME_REGISTERED_ADDRESS });
    }

    const onChangeAddress = ({ name, value, addressType }) => {
        onFormChange({ name, value, level: "address_details", type: addressType });
    }

    const getValueAddressType = (addressType, keyName) => { 
        // const address = secD?.address_details.find((each) => addressType === each?.address_type) || {};
        const address = secD?.address_details.find((each) => addressType === each?.type) || {};
        const val = address[keyName] || "";
        return val;
    }
    
  return (
    <div className="section__container">
        <h4 className="section__container--subtitle">D.1 Australian Company Details</h4>
        {/* placeholder, id, label, type, asterisk, belowText, inputType */}
        <div className="section__container--box">
            <div>
                <CustomTextField
                    placeholder="Full name as registered by ASIC"
                    id="sec-d-fullname-asic"
                    label="Full name as registered by ASIC"
                    type="form-input-full-width"
                    asterisk="*"
                    name="full_name"
                    value={secD?.full_name}
                    onChangeFn={onChangeHandler}
                    formErrors={invFormStatus.errors}
                />
            </div>

            <div className="sub__form">
                <CustomTextField 
                    placeholder="ACN"
                    id="sec-d-acn"
                    label="ACN"
                    asterisk="*"
                    name="acn"
                    value={secD?.acn}
                    onChangeFn={onChangeACN}
                    formErrors={invFormStatus.errors}
                />
                <CustomTextField 
                    placeholder="ABN"
                    id="sec-d-abn"
                    label="ABN"
                    asterisk=""
                    name="abn"
                    value={secD?.abn}
                    onChangeFn={onChangeHandler}
                    formErrors={invFormStatus.errors}
                />
            </div>

            {/* Full address of registered office (PO Box is not acceptable) */}
            <div>
                <h6 className="u-padding-10y">Full address of registered office (PO Box is not acceptable)</h6>
                <div className="sub__form">
                    <CustomTextField
                        placeholder="Unit No./Level"
                        id="sec-d-reg-unit-no"
                        label="Unit No./Level"
                        asterisk=""
                        name="unit_no"
                        value={getValueAddressType(REGISTERED_OFFICE_ADDRESS, "unit_no")}
                        onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: REGISTERED_OFFICE_ADDRESS })}
                        formErrors={errMsgsregOffice}
                    />

                    <CustomTextField
                        placeholder="Street Number"
                        id="sec-d-reg-street-number"
                        label="Street Number"
                        name="street_no"
                        asterisk=""
                        value={getValueAddressType(REGISTERED_OFFICE_ADDRESS, "street_no")}
                        onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: REGISTERED_OFFICE_ADDRESS })}
                        formErrors={errMsgsregOffice}
                    />

                    <CustomTextField
                        placeholder="Street Name"
                        id="sec-d-reg-street-name"
                        label="Street Name"
                        asterisk="*"
                        name="street_name"
                        value={getValueAddressType(REGISTERED_OFFICE_ADDRESS, "street_name")}
                        onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: REGISTERED_OFFICE_ADDRESS })}
                        formErrors={errMsgsregOffice}
                    />
            
                    
                    <CustomTextField
                        placeholder="Suburb/Town"
                        id="sec-d-reg-suburb"
                        label="Suburb/Town"
                        type="form-input"
                        name="suburb"
                        asterisk="*"
                        value={getValueAddressType(REGISTERED_OFFICE_ADDRESS, "suburb")}
                        onChangeFn={({ name, value }) => onChangeAddress({ name, value, addressType: REGISTERED_OFFICE_ADDRESS })}
                        formErrors={errMsgsregOffice}
                    /> 

                    <div className="form-input country__dropdown">
                        <label>State <span>*</span></label>
                        <Select
                            className={errMsgsregOffice?.state?.length && 'incorrect'}
                            id="select-dropdown-country"
                            name="state"
                            autoFocus={false}
                            placeholder="Select a State"
                            options={ausStateOptions()}
                            value={ausStateValue()}
                            onChange={onChangeAusStates}                    />
                        {errMsgsregOffice?.state?.length?
                        <div className="input__desc">
                            {renderErrorTxt(errMsgsregOffice?.state)}
                        </div>: null}
                    </div>
                    
                    <CustomTextField
                        placeholder="Postcode"
                        id="sec-d-reg-postcode"
                        label="Postcode"
                        type="form-input"
                        name="postcode"
                        asterisk="*"
                        value={getValueAddressType(REGISTERED_OFFICE_ADDRESS, "postcode")}
                        onChangeFn={({  name, value }) => onChangeAddress({ name, value, addressType: REGISTERED_OFFICE_ADDRESS})}
                        formErrors={errMsgsregOffice}
                    /> 

                    <div className="country__dropdown u-margin-bottom-3">
                        <label>Country <span>*</span></label>
                        <Select
                            className={errMsgsregOffice?.country?.length && 'incorrect'}
                            id="select-dropdown-country"
                            name="country"
                            autoFocus={false}
                            options={countryOptions}
                            placeholder="Select a Country"
                            value={countryValue()}
                            onChange={onChangeSelectHandler}
                        />

                        {errMsgsregOffice?.country?.length?
                        <div className="input__desc">
                            {renderErrorTxt(errMsgsregOffice?.country)}
                        </div>: null}
                    </div>
                </div>

                {/* Postal address is the same as the registered office */}
                <div className="u-margin-bottom-m">
                    <CustomCheckbox 
                        name="is_address_of_registered_place_same_as_primary"
                        val={PRINCIPAL_PLACE_SAME_REGISTERED_ADDRESS}
                        id="principal-place-same-registered-office"
                        styleType="dark"
                        content="Postal address is the same as the registered office"
                        onChangeFn={handleChangeCheckbox}
                        checked={secD?.is_address_of_registered_place_same_as_primary === PRINCIPAL_PLACE_SAME_REGISTERED_ADDRESS}
                    />
                    {invFormStatus.errors?.is_address_of_registered_place_same_as_primary? <div className="input__desc">
                        {renderErrorTxt(invFormStatus.errors?.is_address_of_registered_place_same_as_primary)}
                    </div>: null} 
                </div>
                
                {/* Full Postal Address */}
                {secD?.is_address_of_registered_place_same_as_primary !== PRINCIPAL_PLACE_SAME_REGISTERED_ADDRESS ?
                <PrincipalPlaceOfBusiness getValueAddressType={getValueAddressType} onChangeAddress={onChangeAddress} ausStateOptions={ausStateOptions} countryOptions={countryOptions}/>: null}
            </div>
        </div>

    </div>
  )
}

export default AustralianCompanyDetails;