import React from 'react';
import { useSelector } from 'react-redux';
import AustralianCompanyDetails from './SubSections/AustralianCompanyDetails';
import RegulatoryDetails from './SubSections/RegulatoryDetails';
import CompanyType from './SubSections/CompanyType';
import DirectorsSection from './SubSections/DirectorsSection';
import IdentificationDocuments from './SubSections/IdentificationDocuments';
import {formatAndPrintDocs} from '../../../../utils/validations/helperFunc';

const SectionDContent = ({ onFormChange, investorFormId, removeDocFromArr }) => {

  const secD = useSelector((store) => store.secD);

  return (
    <div className="section">
        <h2 className="section__heading">Section D: Australian Company</h2>
        <ul>
            <li>Section D to be completed by Australian companies and Australian company trustee only.</li>
        </ul>

        <AustralianCompanyDetails onFormChange={onFormChange} />

        <RegulatoryDetails onFormChange={onFormChange} />

        <CompanyType onFormChange={onFormChange} />

        <DirectorsSection onFormChange={onFormChange} investorFormId={investorFormId} />

        <IdentificationDocuments investorFormId={investorFormId}/>

        {secD?.documents?.length > 0? formatAndPrintDocs(secD?.documents, removeDocFromArr): null} 

    </div>
  )
}

export default SectionDContent;