import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import DocPlaceholder from '../../../Layout/DocPlaceholder';
import { LOADING, SUCCESS, FAILED, IDENTIFICATION_DOCUMENTS_SEC_D, ASIC_EXTRACT_DOC, SEC_D_DOC_EXTRA_LABEL_MAIN } from '../../../../../constants';
import { uploadFiles, deleteFile } from '../../../../../actions/Investor-Application-Form/uploadDocActions';
import CustomDropdown from '../../../Layout/CustomDropdown';
import { addNewDocToArray, removeDocFromArray, updateDocArray } from '../../../../../utils/documentUploads';
import { useToast } from '../../../../CustomToast';

const IdentificationDocuments = ({ investorFormId }) => {
    const toast = useToast();
    const doNotShowExtraLabel = [ASIC_EXTRACT_DOC[0][0]]
    const secD = useSelector((store) => store.secD);
    const [selectedDocType, setSelectedDocType] = useState({label:"Select type of document", value:""})
    const [selectedDirectorNum, setSelectedDirectorNum] = useState({label:`${SEC_D_DOC_EXTRA_LABEL_MAIN} - 1`, value:1})
    const [ uploadedDocs, setUploadedDocs ] = useState([]);

    const onUploadFile = async (file, type={label:'', value:''}, extraDetails={label:'', value:''}) => {
        
        if(file){ 
            const fileName = file.name;                  
            const metadata = JSON.stringify([{"owner":`${SEC_D_DOC_EXTRA_LABEL_MAIN} ${extraDetails.value}`}])
            
            const formData = new FormData();
            formData.append('file', file);
            formData.append('type', type.value);
            if(!doNotShowExtraLabel.includes(type.value)){
                formData.append("metadata", metadata)
            }
            formData.append('section', 'd');
            formData.append('investor_form_id', investorFormId);
            
            const { success, url, data } = await uploadFiles(formData);
            const extradata = {
                loader: success? SUCCESS : FAILED,
                docUrl: url,
                resData: data
            }
            updateDocArray({setUploadedDocs, fileName, extradata})
        }
    }

    const onAddFiles = (files) => {
        //add selected files(by user) to local state - and call api
        addNewDocToArray({
            files,
            uploadedDocs,
            setUploadedDocs,
            onUploadFile,
            doNotShowExtraLabel,
            selectedDocType:{...selectedDocType},
            selectedEntityNum:{...selectedDirectorNum}
        })
    }

    const onClickRemove = (fileName) => { 
        //just remove the file from local state
        removeDocFromArray({fileName, uploadedDocs, setUploadedDocs})
    }

    const updateSelectedOption = (option) => {
        //update selected document type to the state
        setSelectedDocType({...option})
    }

    const updateSelectedDirectorNum = (option) => {
        setSelectedDirectorNum({...option})
    }

    const onClickDel = async({fileName, docData}) => {
        //delete the file from aws & remove the file from local state
        updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: true}})
        const { success } = await deleteFile(docData?.resData);
        success && onClickRemove(fileName);
        success && toast.success("Deleted successfully!");
        success || updateDocArray({setUploadedDocs, fileName, extradata: {delLoader: false}})
        success || toast.error("Please try again!")
    }

    const inestorOptions = () => {
        const tempOptionsArr = [];
        const tempNoOfDirectors = secD?.number_of_directors === -1? 4 : secD?.number_of_directors || 1
        for (let i = 1; i <= tempNoOfDirectors; i++) {
            tempOptionsArr.push([i, `${SEC_D_DOC_EXTRA_LABEL_MAIN} - ${i}`])
        }
        return tempOptionsArr
    }

  return (
    <div className="section__container">
    <h4 className="section__container--subtitle">D.5 Identification Documents</h4>

    <div className="section__container--box">
        <h6>Please provide a copy of the following Identification document:</h6>
        <ul>
            <li>Full ASIC company extract</li>
        </ul>

        <h6>For each Beneficial Owner, provide ONE of the following documents</h6>
        <ul>
            <li>Australian State / Territory driver's licence containing a photograph of the person</li>
            <li>Australian passport (a passport that has expired within the preceding 2 years is acceptable)</li>
            <li>Card issued under a State or Territory for the purpose of proving a person's age containing a photograph of the person</li>
            <li>Foreign passport or similar travel document containing a photograph and the signature of the person*</li>
            <li>Foreign driver's licence that contains a photograph of the person in whose name the document is issued*</li>
        </ul>
        <div className="note__box u-padding-10y">
            <p className="note__box--p">* Documents that are written in a language other than English must be accompanied with an English translation prepared by an Accredited Translator</p>
            <p className="note__box--p">If you are unable to provide one of the above documents, please contact us at <a href="mailto:investors@silcgroup.com">investors@silcgroup.com</a></p>
        </div>

        <h6 className="u-padding-y">Please attach documents</h6>
        <div className="sub__form" style={{gap:16}}>
            <div style={{minWidth: 350}}>
                <CustomDropdown 
                    dropdownOptions={inestorOptions()}
                    selectedOption={selectedDirectorNum}
                    updateSelectedOption={updateSelectedDirectorNum}
                />
            </div>
            <div style={{minWidth: 375}}>
                <CustomDropdown
                    addClass='control-dropdown-height'
                    dropdownOptions={IDENTIFICATION_DOCUMENTS_SEC_D}
                    selectedOption={selectedDocType}
                    updateSelectedOption={updateSelectedOption}
                    extraLabel={`(${SEC_D_DOC_EXTRA_LABEL_MAIN} - ${selectedDirectorNum?.value})`}
                    doNotShowExtraLabelSpecial={doNotShowExtraLabel}
                />
            </div>
        </div>
        {selectedDocType?.value ?
            <div className='u-padding-y'>
                <DocPlaceholder onAddFiles={onAddFiles} uploadedDocs={uploadedDocs} onClickRemove={onClickRemove} onClickDel={onClickDel} uniqId="section-d"/>
            </div>: null
        }
        <div className="note__box">
            <p><span>Note:</span><br /> For definitions of Politically Exposed Person, Controlling Person and Beneficial Owner <a href="https://www.silcgroup.com/definitions/">https://www.silcgroup.com/definitions/</a></p>
        </div>
    </div>
</div>
  )
}

export default IdentificationDocuments;