import React from 'react';
import { useSelector } from 'react-redux';
import CustomTextField from '../../Layout/CustomTextField';

const Directors = ({ directorId, directorDetails, investorFormId, onChangeDirector, itemIndex }) => {
    const invFormStatus = useSelector((store) => store.invFormStatus)

    const errMsgs = {
        name : invFormStatus.errors?.name? invFormStatus.errors?.name[itemIndex]: [],
        surname : invFormStatus.errors?.surname? invFormStatus.errors?.surname[itemIndex]: [],
    }

    const onChangeIn = ({ name, value }) => {
        onChangeDirector({
            name,
            value,
            directorId
        });
    }
    
    return (
    <div key={directorId}>
        <h6 className="u-padding-10y">{`Director ${itemIndex + 1}`}</h6>
            <div className="sub__form">
                <CustomTextField
                    placeholder="Given Name"
                    id={`sec-d-director${directorId}-given-name`}
                    label="Given Name"
                    asterisk="*"
                    name="name"
                    value={directorDetails?.name || ""}
                    onChangeFn={onChangeIn}
                    formErrors={errMsgs}
                />
                <CustomTextField
                    placeholder="Surname"
                    id={`sec-d-director${directorId}-surname`}
                    label="Surname"
                    asterisk="*"
                    name="surname"
                    value={directorDetails?.surname || ""}
                    onChangeFn={onChangeIn}
                    formErrors={errMsgs}
                />    
            </div>
    </div>
    )

}

export default Directors