import React from 'react';
import { useSelector } from 'react-redux';
import { PROPRIETARY_COMPANY_TYPE, PUBLIC_COMPANY_TYPE } from '../../../../../constants';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';

const CompanyType = ({ onFormChange }) => {

  const secD = useSelector((store) => store.secD);
  const invFormStatus = useSelector((store) => store.invFormStatus)

  const onChangeFn = ({ name, value }) => {
    const convrtToNum = value && parseInt(value);
    onFormChange({ name, value: convrtToNum });
  }
  
  return (
    <div className="section__container">
      <h4 className="section__container--subtitle">D.3 Company Type</h4>

      <div className="section__container--box">
        <div>
          <h6>Is the company type public or proprietary?<span>*</span></h6>
          <CustomRadioInput
            name="company_type"
            id="sec-d-public-company"
            content="Public (companies whose name does NOT include the words “Pty Ltd” or “Proprietary Ltd”; generally listed companies)"
            val={PUBLIC_COMPANY_TYPE}
            checked={secD?.company_type === PUBLIC_COMPANY_TYPE}
            onChangeFn={onChangeFn}
          />

          <CustomRadioInput
            name="company_type"
            id="sec-d-proprietary"
            content="Proprietary (companies whose name ends with “Proprietary Ltd” or “Pty Ltd”; also known as private companies)"
            val={PROPRIETARY_COMPANY_TYPE}
            checked={secD?.company_type === PROPRIETARY_COMPANY_TYPE}
            onChangeFn={onChangeFn}
          />
          {invFormStatus.errors?.company_type? <div className="input__desc">
            {renderErrorTxt(invFormStatus.errors?.company_type)}
          </div>: null}
        </div>
      </div>
    </div>
  )
}

export default CompanyType;